.TTT {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f5f5f573;
  height: 100%;
}

.lobby-tab {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f5f5f573;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  width: 100%;
  height: 100%;
}

.lobby-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f5f5f573;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  width: 80%;
  padding: 1%;
}

.create-button {
  background-color: #173618;
  color: white;
  border: none;
  border-radius: 25px;
  padding: 20px 20px;
  font-size: clamp(1.2rem, 2vw, 2rem);
  cursor: pointer;
}

.Lobby-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f5f5f573;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}

.lobbyLI {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  margin: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  cursor: pointer;
  min-width: 80%;
  transition: 0.5s;
}

.lobbyLI:hover {
  background-color: #87b8f8;
}

.join-button {
  background-color: #173618;
  color: white;
  border: none;
  border-radius: 25px;
  padding: 10px 20px;
  font-size: 1.2rem;
  max-width: 20vw;
  cursor: pointer;
}


.quit-button {
  background-color: #173618;
  color: white;
  border: none;
  border-radius: 25px;
  padding: 10px 20px;
  font-size: 1.2rem;
  max-width: 20vw;
  cursor: pointer;
}

.board {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  aspect-ratio: 1/1;
  margin: 10px;
}

.tile {
  display: flex;
  background-color: #f0f0f0;
  align-items: center;
  justify-content: center;
  font-size: 5rem;
  min-height: 16vh;
  max-height: 16vh;
  min-width: 16vh;
  max-width: 16vh;
  border: 5px solid #ccc;
  cursor: pointer;
}

.X {
  color: #f00;
}

.O {
  color: #00f;
}