.chat-container {
  display: flex;
  overflow-y: scroll;
  flex-direction: column;
  margin-top: auto;
  align-items: center;
  background-color: #f5f5f573;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  padding: 1%;
}

  
.chat-input {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  background-color: #f5f5f573;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  padding-bottom: 10px;
}

input[type="text"] {
  width: 90vw;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 5px solid #ccc;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  border-radius: 25px;
}

.send-button {
  background-color: #173618;
  color: white;
  border: none;
  border-radius: 25px;
  padding: 13px 20px;
  font-size: 1.2rem;
  max-width: 20%;
  cursor: pointer;
  position: absolute;
  right: 3vw;
  transition: ease-in-out 0.2s;
}

  
.send-button:hover {
  background-color: #27722a;
}

.sent {
  align-self: flex-end;
  background-color: #282c34;
  border-radius: 15px;
  padding: 10px;
  margin: 5px;
  max-width: 80%;
  word-wrap: break-word;
  min-width: 20%;
}

.sent h1 {
  font-size: 2rem;
  color: #27722a;
  margin: 0;
}

.sent p {
  font-size: 1.3rem;
  color: white;
}

.received {
  align-self: flex-start;
  background-color: #282c34;
  border-radius: 15px;
  padding: 10px;
  margin: 5px;
  max-width: 80%;
  word-wrap: break-word;
  min-width: 20%;
}

.received h1 {
  font-size: 2rem;
  color: #1a65c7;
  margin: 0;
}

.received p {
  font-size: 1.3rem;
  color: white;
}



